.container {
  width: 85vw;
  margin: 30px auto 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 20px;
}

@media (max-width: 1069px) {
  .container {
    grid-template-columns: 1fr 1fr;
  }
}

@media (max-width: 763px) {
  .container {
    grid-template-columns: 1fr;
  }
}
